export async function FORMULA(description, contextRange) {
  const desc = toScalarString(description).trim();
  if (!desc) return cfError(CustomFunctions?.ErrorCode?.invalidValue, "La description est vide.");

  const context = contextRange != null && isMatrix(contextRange) ? matrixToTSV(contextRange) : "";
  const user = context
    ? `Contexte (TSV):\n${context}\n\nDescription:\n${desc}`
    : desc;

  const locale = getExcelLocale();
  const formulaLang = isFrenchLocale(locale) ? "fr" : "en";

  const r = await aiGenerate({
    system: sysFormula(formulaLang),
    user,
    webSearch: false,
    functionName: "AI.FORMULA",
    functionCall: buildFormulaCall("AI.FORMULA", [description, contextRange])
  });

  if (!r.ok) return cfError(CustomFunctions?.ErrorCode?.notAvailable, r.message || "Erreur AI.FORMULA.");

  // Extract first formula starting with "="
  const text = (r.text || "").trim();
  const m1 = text.match(/=[^\n\r]+/);
  const formula = (m1 ? m1[0] : text).trim();

  if (!formula.startsWith("=")) {
    return cfError(CustomFunctions?.ErrorCode?.notAvailable, "La rponse n'est pas une formule Excel valide.");
  }
  return truncateForCell(formula);
}

export async function TABLE(description, contextRange) {
  const desc = toScalarString(description).trim();
  if (!desc) return cfError(CustomFunctions?.ErrorCode?.invalidValue, "La description est vide.");

  const context = contextRange != null && isMatrix(contextRange) ? matrixToTSV(contextRange) : "";

  const schema = {
    type: "object",
    additionalProperties: false,
    required: ["headers", "rows"],
    properties: {
      headers: { type: "array", items: { type: "string" } },
      rows: {
        type: "array",
        items: {
          type: "array",
          items: { type: "string" }
        }
      }
    }
  };

  const user = [
    "Build a table for Excel based on the description and optional context.",
    "Return JSON { headers: [...], rows: [[...], ...] }",
    context ? `\nContext (TSV):\n${context}` : "",
    `\nDescription:\n${desc}`
  ].join("\n");

  const r = await aiGenerate({
    system: sysTable(),
    user,
    responseMimeType: "application/json",
    responseJsonSchema: schema,
    functionName: "AI.TABLE",
    functionCall: buildFormulaCall("AI.TABLE", [description, contextRange])
  });

  if (!r.ok) return cfError(CustomFunctions?.ErrorCode?.notAvailable, r.message || "Erreur AI.TABLE.");

  let obj = null;
  try {
    obj = parseJsonSafe(r.text);
  } catch {
    // If strict parsing fails, try to just find the first JSON-like object
    try {
      const match = r.text.match(/\{[\s\S]*\}/);
      if (match) obj = JSON.parse(match[0]);
      else throw new Error();
    } catch {
      return cfError(CustomFunctions?.ErrorCode?.notAvailable, "Impossible de parser le JSON renvoy par l'IA.");
    }
  }

  const headers = Array.isArray(obj?.headers) ? obj.headers.map((x) => truncateForCell(toScalarString(x))) : [];
  const rows = Array.isArray(obj?.rows) ? obj.rows : [];

  const width = headers.length || (Array.isArray(rows?.[0]) ? rows[0].length : 0) || 1;
  const safeHeaders = headers.length ? headers : Array.from({ length: width }, (_, i) => `Col${i + 1}`);

  const out = [safeHeaders];

  for (const r0 of rows) {
    const row = Array.isArray(r0) ? r0.map((x) => truncateForCell(toScalarString(x))) : [];
    // pad / truncate to width
    while (row.length < safeHeaders.length) row.push("");
    if (row.length > safeHeaders.length) row.length = safeHeaders.length;
    out.push(row);
  }

  return out;
}

export async function FILL(exampleRange, targetRange, instruction) {
  const instr = toScalarString(instruction).trim();
  if (!instr) return cfError(CustomFunctions?.ErrorCode?.invalidValue, "L'instruction est vide.");

  const ex = normalizeToMatrix(exampleRange);
  const tgt = normalizeToMatrix(targetRange);

  const nRows = tgt.length;
  if (nRows <= 0) return [[""]];

  if (nRows > MAX_BATCH_CELLS) {
    return cfError(
      CustomFunctions?.ErrorCode?.invalidValue,
      `Plage cible trop grande (max ${MAX_BATCH_CELLS} lignes) pour AI.FILL.`
    );
  }

  const schema = {
    type: "object",
    additionalProperties: false,
    required: ["values"],
    properties: {
      values: { type: "array", items: { type: "string" } }
    }
  };

  const user = [
    `Instruction: ${instr}`,
    "",
    "Examples (TSV):",
    matrixToTSV(ex, 6000),
    "",
    "Target rows (TSV):",
    matrixToTSV(tgt, 6000),
    "",
    `Return JSON with values array of length ${nRows} (one value per target row).`
  ].join("\n");

  const r = await aiGenerate({
    system: sysFill(DEFAULT_LANG, nRows),
    user,
    responseMimeType: "application/json",
    responseJsonSchema: schema,
    functionName: "AI.FILL",
    functionCall: buildFormulaCall("AI.FILL", [exampleRange, targetRange, instruction])
  });

  if (!r.ok) return cfError(CustomFunctions?.ErrorCode?.notAvailable, r.message || "Erreur AI.FILL.");

  let values = [];
  try {
    const obj = JSON.parse(r.text);
    if (Array.isArray(obj?.values)) values = obj.values.map((x) => toScalarString(x));
  } catch {
    values = r.text.split("\n").map((x) => x.trim());
  }

  while (values.length < nRows) values.push("");
  if (values.length > nRows) values = values.slice(0, nRows);

  return values.map((v) => [truncateForCell(v)]);
}

export async function SUMMARIZE(textOrRange, mode) {
  const summarizeMode = parseSummarizeMode(mode);
  const m = normalizeToMatrix(textOrRange);

  if (summarizeMode === 1) {
    const flat = flattenMatrix(m, 2000)
      .map((s) => s.trim())
      .filter(Boolean);

    if (!flat.length) return [[""]];

    const joined = flat.join("\n");
    const user = `Content:\n${joined}`;

    const r = await aiGenerate({
      system: sysSummarize(DEFAULT_LANG),
      user,
      webSearch: false,
      functionName: "AI.SUMMARIZE",
      functionCall: buildFormulaCall("AI.SUMMARIZE", [textOrRange, mode])
    });

    if (!r.ok) return cfError(CustomFunctions?.ErrorCode?.notAvailable, r.message || "Erreur AI.SUMMARIZE.");
    return [[truncateForCell(r.text)]];
  }

  const flat = flattenMatrix(m, 2000).map((s) => s.trim());
  const expectedItems = flat.length;
  const isBatch = expectedItems > 1;

  if (!expectedItems) return [[""]];

  const schema = {
    type: "object",
    additionalProperties: false,
    required: ["items"],
    properties: {
      items: { type: "array", items: { type: "string" } }
    }
  };

  const lines = flat.map((t, i) => `${i + 1}. ${t}`);
  const user = isBatch
    ? [
      "Summarize each item for Excel.",
      `Return JSON with items array of length ${expectedItems}. Use empty string for empty inputs.`,
      "",
      ...lines
    ].join("\n")
    : `Summarize for Excel:\n${flat[0] || ""}`;

  const r = await aiGenerate({
    system: sysSummarize(DEFAULT_LANG, expectedItems),
    user,
    responseMimeType: isBatch ? "application/json" : undefined,
    responseJsonSchema: isBatch ? schema : undefined,
    functionName: "AI.SUMMARIZE",
    functionCall: buildFormulaCall("AI.SUMMARIZE", [textOrRange, mode])
  });

  if (!r.ok) return cfError(CustomFunctions?.ErrorCode?.notAvailable, r.message || "Erreur AI.SUMMARIZE.");

  let items = [];
  if (isBatch) {
    try {
      const obj = parseJsonSafe(r.text);
      if (Array.isArray(obj?.items)) items = obj.items.map((x) => toScalarString(x));
    } catch {
      items = r.text.split("\n").map((x) => x.trim());
    }
  } else {
    try {
      const obj = parseJsonSafe(r.text);
      if (Array.isArray(obj?.items)) items = obj.items.map((x) => toScalarString(x));
    } catch {
      // ignore and fallback to plain text
    }
    if (!items.length) items = [toScalarString(r.text).trim()];
  }

  while (items.length < flat.length) items.push("");
  if (items.length > flat.length) items = items.slice(0, flat.length);

  const out = [];
  let k = 0;
  for (let i = 0; i < m.length; i++) {
    const row = [];
    for (let j = 0; j < (m[i] || []).length; j++) {
      row.push(truncateForCell(items[k++] || ""));
    }
    out.push(row);
  }
  return out;
}

export function COUNT(range, value) {
  try {
    const m = normalizeToMatrix(range);
    const needle = toScalarString(value).toLowerCase();

    let count = 0;
    for (const row of m) {
      const r = Array.isArray(row) ? row : [row];
      for (const cell of r) {
        if (toScalarString(cell).toLowerCase() === needle) count++;
      }
    }
    return count;
  } catch (e) {
    return cfError(CustomFunctions?.ErrorCode?.invalidValue, e?.message || "Erreur AI.COUNT.");
  }
}

// ---- Registration (robust for shared runtime timing) ----

function registerAll() {
  // Associate function names exactly as used in functions.json (ids map to namespace AI.*)
  try {
    if (typeof CustomFunctions === "undefined" || !CustomFunctions?.associate) return false;

    const safeAssociate = (id, fn) => {
      try {
        CustomFunctions.associate(id, fn);
        return true;
      } catch {
        return false;
      }
    };

    let ok = true;
    ok = safeAssociate("AI.KEYSTATUS", KEY_STATUS) && ok;
    ok = safeAssociate("AI.TEST", TEST) && ok;
    ok = safeAssociate("AI.ASK", ASK) && ok;
    ok = safeAssociate("AI.WEB", WEB) && ok;
    ok = safeAssociate("AI.TRANSLATE", TRANSLATE) && ok;
    ok = safeAssociate("AI.CLASSIFY", CLASSIFY) && ok;
    ok = safeAssociate("AI.EXTRACT", EXTRACT) && ok;
    ok = safeAssociate("AI.CLEAN", CLEAN) && ok;
    ok = safeAssociate("AI.CONSISTENT", CONSISTENT) && ok;
    ok = safeAssociate("AI.FORMULA", FORMULA) && ok;
    ok = safeAssociate("AI.TABLE", TABLE) && ok;
    ok = safeAssociate("AI.FILL", FILL) && ok;
    ok = safeAssociate("AI.SUMMARIZE", SUMMARIZE) && ok;
    ok = safeAssociate("AI.COUNT", COUNT) && ok;

    return ok;
  } catch {
    return false;
  }
}

// Poll until CustomFunctions is available (Office.js loads after functions.js in taskpane.html)
const regTimer = setInterval(() => {
  if (registerAll()) clearInterval(regTimer);
}, 200);
